//对称加密和解密使用crypto-js--前端解密使用
//非对称加密使用jsencrypt,--发送到后端使用
import * as CryptoJS from 'crypto-js/crypto-js';
// import { JSEncrypt } from 'jsencrypt'
// 或者下面这种方式引入
import JSEncrypt from 'jsencrypt/bin/jsencrypt'

const  key = 'o08YQii9QF5MuzYj'
//与后端约定的解密key
// const key = '';
//非对称加密的公钥
let publicKey = 'MFwwDQYJKoZIhvcNAQEBBQADSwAwSAJBANWnvOm0wS9hsY9xiAs2G09Ah88zaJHMwEdXiVBa+pCJVSlJdSglewnHFWuT/4GBMTbcfusTQWChjDNn3trzWwcCAwEAAQ=='




//对称加密(获取数据后用该方法解密)
export function decAdd(data){ //未测试
    let keyHex = CryptoJS.enc.Base64.parse(key);
    let encrypted = CryptoJS.DES.encrypt(data, keyHex, {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7
    });
    return encrypted.toString(CryptoJS.enc.Utf8);
}

//对称解密(获取数据后用该方法解密)
export function decrypt(data) {
    if(!data){
        return null
    }
  const keyHex = CryptoJS.enc.Utf8.parse(key);
  const decrypted = CryptoJS.DES.decrypt({
    ciphertext: CryptoJS.enc.Hex.parse(data)
  }, keyHex, {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7
  });
  //以utf-8的形式输出解密过后内容
  return decrypted.toString(CryptoJS.enc.Utf8);
}
 //非对称加密（数据传到后端）
 export function encryptedData(data) {
 // 新建 JSEncrypt 对象
 let encryptor = new JSEncrypt()
 // 设置公钥
 encryptor.setPublicKey(publicKey)
 // 加密数据
 return encryptor.encrypt(data)
}



